.doctor {
  padding-top: vw(59);
  @include for-lg{
    padding-top: vww(59);
  }
  @include for-mb{
    padding-top: vw2(70);
    position: relative;
  }

  &__title {
    margin: 0 auto vw(59);
    font-size: vw(48);
    line-height: vw(56);
    text-transform: uppercase;
    text-align: center;
    font-weight: 500;
    @include for-lg{
      margin: 0 auto vww(59);
      font-size: vww(48);
      line-height: vww(56);
    }
    @include for-mb{
      margin: 0 auto vw2(30);
      font-size: vw2(48);
      line-height: vw2(56);
    }
  }

  &__wrapper {
    display: flex;
    @include for-mb {
      flex-direction: column-reverse
    }
  }

  &__author {
    @include for-mb{
      display: flex;
      justify-content: center;
      align-items: center;
      margin-bottom: vw2(25);
    }
    &-image {
      width: vw(372);
      height: vw(389);
      margin: vw(-79) vw(-49) vw(-92) vw(-50);
      @include for-lg{
        width: vww(372);
        height: vww(389);
        margin: vww(-79) vww(-49) vww(-92) vww(-50);
      }
      @include for-mb{
        margin:inherit;
        width: vw2(240);
        height: vw2(240);
      }

      img {
        display: block;
        width: 100%;
        height: auto;
      }
    }
    &-name {
      width: vw(220);
      margin: 0 auto;
      font-size: vw(18);
      line-height: vw(20);
      font-weight: 700;
      font-style: italic;
      text-align: center;
      color: $white;
      @include for-lg{
        width: vww(220);
        margin: 0 auto;
        font-size: vww(18);
        line-height: vww(20);
      }
      @include for-mb{
        margin:inherit;
        text-align: left;
        font-size: vw2(18);
        line-height: vw2(20);
        width: vw2(220);
      }

      span {
        display: block;
        font-size: vw(14);
        font-weight: 300;
        @include for-lg{
           font-size: vww(14);
         }
        @include for-mb{
          font-size: vw2(14);
        }
      }
    }
  }

  &__content {
    width: vw(607);
    font-size: vw(24);
    line-height: 1.16;
    font-style: italic;
    @include for-lg{
      width: vww(607);
      font-size: vww(24);
    }
    @include for-mb{
      width: vw2(538);
      font-size: vw2(24);
      line-height: vw2(28);
      margin-left: auto;
      margin-right: auto;
      margin-bottom: vw2(-10);
    }

    p {
      margin: 0;
    }
  }

  &__btn {
    display: block;
    width: vw(248);
    margin-left: auto;
    padding: vw(16) vw(10) vw(8);

    font-size: vw(30);
    line-height: 1.16;
    text-align: center;
    font-weight: 500;
    font-style: normal;
    color: $color-blue2;
    text-transform: uppercase;

    box-shadow: inset vw(3) vw(6) vw(18) rgba(0, 0, 0, 0.48);
    border-radius: vw(21);
    background: $color-turquoise;
    @include for-lg{
      width: vww(248);
      margin-left: auto;
      padding: vww(16) vww(10) vww(8);

      font-size: vww(30);

      box-shadow: inset vww(3) vww(6) vww(18) rgba(0, 0, 0, 0.48);
      border-radius: vww(21);
    }
    @include for-mb{
      width: vw2(248);
      padding: vw2(16) vw2(10) vw2(8);

      font-size: vw2(30);

      box-shadow: inset vw2(3) vw2(6) vw2(18) rgba(0, 0, 0, 0.48);
      border-radius: vw2(21);
      position: absolute;
      bottom:0;
      left:50%;
      margin-left: vw2(-124);
    }
  }
}
